.sidebar {
  position: relative;
  overflow: hidden;
  height: 200%;
  width: 200px;
  background: white;
  padding: 20px;
  font-size: 15px;
}

.sidebar-toggle {
  position: absolute;
  margin: 1px;
  color:#161616;
  z-index: 100;
  font-size: 1.5em;
  cursor: pointer;
}

.login-form-forgot {
  float: right;
}

.login-form-button {
  width: 100%;
}

.ant-avatar {
  width: 50px;
  height: 50px;
  line-height: 5px;
  border-radius: 50%;
  font-size: 3em !important;
}
.shreya{
  display:flex;
   position: absolute;
  left: 24px;
  top: 122px;
  z-index: 1;
    
}


.middle .ant-form-item-control {
  display: flex;
  justify-content:center;
}

.sidebar .ant-form-item {
  margin: 0;
}

.sidebar .ant-form-item-control {
  line-height: 29px;
}


